<template>
  <div class="frofile_container">
    <head-top :head-title="profiletitle" goBack="true"></head-top>
    <section>
        <section class="profile-number">
            <router-link :to="userInfo&&userInfo.user_id ? '/profile/info' : '/login'" class="profile-link">
                <img :src="imageBaseUrl+userInfo.avatar" class="privateImage" alt="" v-if="userInfo&&userInfo.user_id">
                <span class="privateImage" v-else>
                    <svg class="privateImage-svg">
                        <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#avatar-default"></use>
                    </svg>
                </span>
                <div class="user-info">
                    <p>{{username}}</p>
                    <p>
                        <span class="user-icon">
                            <svg class="icon-mobile" fill="#fff">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#mobile"></use>
                            </svg>
                        </span>
                        <span>{{mobile}}</span>
                    </p>
                </div>
                <span class="arrow">
                    <svg class="arrow-svg" fill="#fff">
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                    </svg>
                </span>
            </router-link>
            <section class="info-data">
                <ul class="clear">
                    <router-link class="info-data-link" tag="li" to="/balance">
                        <span class="info-data-top"><b>{{parseInt(balance).toFixed(2)}}</b> 元</span>
                        <span class="info-data-bottom">我的余额</span>
                    </router-link>
                    <router-link class="info-data-link" tag="li" to="/benefit">
                        <span class="info-data-top"><b style="color:red">{{count}}</b>个</span>
                        <span class="info-data-bottom">我的优惠卷</span>
                    </router-link>
                    <router-link class="info-data-link" tag="li" to="/points">
                        <span class="info-data-top"><b style=" color: springgreen">{{pointNumer}}</b>分</span>
                        <span class="info-data-bottom">我的积分</span>
                    </router-link>
                </ul>
            </section>
            <section class="profile-1rete">
                <!-- 我的订单 -->
                <router-link class="myorder" to="/order">
                <!-- aside标签定义其所处内容之外的内容 -->
                    <aside>
                        <svg fill="#4aa5f0">
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#order"></use>
                        </svg>
                    </aside>
                    <div class="myorder-div">
                        <span>我的订单</span>
                        <span class="myorder-divsvg">
                            <svg fill="#bbb">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                            </svg>
                        </span>
                    </div>
                </router-link>
                <a href='https://home.m.duiba.com.cn/#/chome/index' class="myorder">
                <!-- aside标签定义其所处内容之外的内容 -->
                    <aside>
                        <svg fill="#fc7b53">
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#point"></use>
                        </svg>
                    </aside>
                    <div class="myorder-div">
                        <span>积分商城</span>
                        <span class="myorder-divsvg">
                            <svg fill="#bbb">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                            </svg>
                        </span>
                    </div>
                </a>
                <router-link class="myorder" to="/vipcard">
                <!-- aside标签定义其所处内容之外的内容 -->
                    <aside>
                        <svg fill="#ffc636">
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#vip"></use>
                        </svg>
                    </aside>
                    <div class="myorder-div">
                        <span>饿了么会员卡</span>
                        <span class="myorder-divsvg">
                            <svg fill="#bbb">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                            </svg>
                        </span>
                    </div>
                </router-link>
            </section>
            <section class="profile-1rete">
                <router-link class="myorder" to="/service">
                <!-- aside标签定义其所处内容之外的内容 -->
                    <aside>
                        <svg fill="#4aa5f0">
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#service"></use>
                        </svg>
                    </aside>
                    <div class="myorder-div">
                        <span>服务中心</span>
                        <span class="myorder-divsvg">
                            <svg fill="#bbb">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                            </svg>
                        </span>
                    </div>
                </router-link>
                <router-link class="myorder" to="/download">
                <!-- aside标签定义其所处内容之外的内容 -->
                    <aside>
                        <svg fill="#4aa5f0">
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#download"></use>
                        </svg>
                    </aside>
                    <div class="myorder-div">
                        <span>下载饿了么App</span>
                        <span class="myorder-divsvg">
                            <svg fill="#bbb">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                            </svg>
                        </span>
                    </div>
                </router-link>
            </section>
        </section>
    </section>
    <food-guide></food-guide>
    <transition name="profile-view" mode="out-in">
        <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import headTop from '../../components/header/header';
import foodGuide from '../../components/footer/footGuide'
import {mapState,mapMutations} from 'vuex'
import {getImgPath} from '../../components/common/mixin'
import {imageBaseUrl} from '../../config/env'
export default {
    data(){
        return{
            imageBaseUrl,
            profiletitle:'我的',
            username:'登陆/注册',
            resetname:'',
            mobile:'暂无绑定手机号',
            balance:0,//我的余额
            count:0,//我的优惠卷个数
            pointNumer:0,//积分数
            avatr:'',//头像地址

        }
    },
    created () {
        this.initdata();
    },
    computed: {
        ...mapState([
            'userInfo'
        ]),
        imgpath:function()
        {
            let path;
            if(this.avatr.indexOf('/')!==-1)
            {
                path=imageBaseUrl+this.avatr;
            }else{
                path=this.getImgPath(this.avatr);
            }
            this.SAVE_AVANDER(path);
            return path;
        }
    },methods: {
        ...mapMutations([
            'SAVE_AVANDER'
        ]),
        initdata()
        {
            if(this.userInfo&&this.userInfo.user_id)
            {
                this.avatr=this.userInfo.avatar;
                this.username=this.userInfo.username;
                this.mobile=this.userInfo.mobile || '暂无绑定手机号';
                this.balance=this.userInfo.balance;
                this.count=this.userInfo.gift_amount;
                this.pointNumer=this.userInfo.point;
            }else{
                this.username="登陆/注册";
                this.mobile='暂无绑定手机号';
            }
        }
    },components: {
        headTop,
        foodGuide
    },mixins: [getImgPath],
    watch: {
        userInfo:function(value)
        {
            this.initdata();
        }
    }
}
</script>

<style lang="scss" scopde>
@import '../../style/mixin.scss';
    .profile_page{
        p, span{
            font-family: Helvetica Neue,Tahoma,Arial;
        }
    }
.profile-number{
    margin-top: 1.9rem;
}
.profile-link{
    background: $blue;
    display: block;
    padding:0.766rem;
    display: flex;
    align-items: center;
    .privateImage{
        @include wh(2.5rem,2.5rem);
        border:0.025rem solid $blue;
        border-radius: 50%;
        display:inline-block;
        .privateImage-svg{
            background:$fc;
            border-radius:50%;
            @include wh(2rem,2rem);
        }
    }
    .user-info{
        padding-left:0.2rem;
        flex:1;
        p{
            @include sc(0.8rem,#fff);
            margin-bottom: .5rem;
        }
        .user-icon{
            svg{
                @include wh(.8rem,.8rem);
            }
        }
        p:nth-of-type(2){
            display: flex;
            align-items: center;
            margin-top: -0.2rem;
            span:nth-of-type(2)
            {
                @include sc(0.5rem,#fff);
                padding-left: 0.1rem;
            }
        }
    }
    .arrow{
        svg{
            @include wh(.8rem,.8rem);
        }
    }
}
.info-data{
    background: #fff;
    .clear{
        display: flex;
        .info-data-link{
            display: flex;
            text-align: center;
            border-right: 0.025rem solid #f1f1f1;;
            flex:1;
            padding:0.3rem;
            flex-direction: column;
            .info-data-top{
                font-size: 0.7rem;
                b{
                    @include sc(1.2rem,#f90);
                    
                }
            }
            .info-data-bottom{
                padding:0.2rem 0;
                @include sc(0.7rem,#666);
            }
        }
    }
}
.profile-1rete{
    margin-top: 0.4rem;
    background:#fff;
    .myorder{
        padding:0.4rem;
        display: flex;
        align-items: center;
        aside{
            flex:1;
            align-items: center;
            svg{
                @include wh(1rem,1rem);
            }
        }
        .myorder-div{
            display: flex;
            flex:12;
            justify-content: space-between;
            border-bottom: 0.025rem solid #f1f1f1;
            span{
                @include sc(0.6rem,#666);
                padding-top:0.3rem;
                padding-bottom:0.5rem;
            }
            svg{
                @include wh(.5rem,.5rem);
            }
        }
    }
}
.profile-view-enter-active,.profile-view-Leave-active{
    transition: all .4s;
}
.profile-view-enter,.profile-view-leave-active{
 transform: translate3d(2rem, 0, 0);
        opacity: 0;
}
</style>



